/* *** FONTS *** */
/* latin-ext */
@font-face {
  font-family: 'DINOT-Regular';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('DINOT Regular'), local('DINOT-Regular'), url(https://assets.contentstack.io/v3/assets/blte63f7056be4da683/blt18dd5a021f4af85d/5f67d680d70e764f32921bea/DINOT-Regular.woff2) format('woff2');
  unicode-range: U+0100-024F, U+0259, U+1E00-1EFF, U+2020, U+20A0-20AB, U+20AD-20CF, U+2113, U+2C60-2C7F, U+A720-A7FF;
}
/* latin */
@font-face {
  font-family: 'DINOT-Regular';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('DINOT Regular'), local('DINOT-Regular'), url(https://assets.contentstack.io/v3/assets/blte63f7056be4da683/blt18dd5a021f4af85d/5f67d680d70e764f32921bea/DINOT-Regular.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}

/* *** GLOBAL *** */
html {
  overflow: hidden; /* need this to prevent page from scrolling! */
}

body {
  font-size: 16px;
  line-height: 1em;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
  font-family: 'DINOT-Regular', Roboto, -apple-system, BlinkMacSystemFont, 'Segoe UI', Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  position: relative;
  background: #000000;
}

main {
  overflow:hidden;
}

.nav-container {
  width: 100%;
}

.nav-container .navlink {
  position: absolute;
  top: 12px;
  opacity: 1;
  visibility: visible;
  color: black !important;
  text-shadow: 0 0 1px white;
  font-size: 0.8em;
}

.nav-container .navlink-right {
  right: 12px;
}

.nav-container .navlink-left {
  left: 12px;
}

.nav-container .navlink-center {
  width: 30px;
  height: 30px;
  top: 5px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url(../assets/spyglass_logo.svg);
  background-size: 100% 100%;
}

a {
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
}